
@font-face {
    font-family: 'IE-Lintel-Regular';
    src: url("../fonts/Lintel-Regular.eot");
    src: url("../fonts/Lintel-Regular.eot?#iefix") 
        format("embedded-opentype"), url("../fonts/lintel-regular.woff") 
        format("woff"), url("../fonts/Lintel-Regular.ttf") 
        format("truetype"), url("../fonts/Lintel-Regular.svg#lintelregular") format("svg");
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'lintelBold';
    src: url("../fonts/lintelBold.eot");
    src: url("../fonts/lintelBold.eot?#iefix") 
        format("embedded-opentype"), url("../fonts/lintelBold.woff") 
        format("woff"), url("../fonts/lintelBold.ttf") 
        format("truetype"), url("../fonts/lintelBold.lintelBold") format("svg");
    font-weight: normal;
    font-style: normal;
}
body{
    font-family: 'IE-Lintel-Regular';
}



